const Home = () => {

    const [screenWidth, setScreenWidth] = useState(window.innerWidth);

    useEffect(() => {
        // Update the document title using the browser API
    }, [screenWidth]);

    const updateSize = ()=> {
        setScreenWidth(window.innerWidth);
    }

    window.addEventListener('resize', updateSize);

    if (screenWidth > 900){
        // show mobile layout
        return (
            <Grid container>
                <Header/>
                <GridSchool/>
                <GridCard/>
                <Roadmap/>
                <Faq/>
                <SchoolTeam/>
                <Footer/>
            </Grid>
        );
    } else {
        return (
            <Grid container>
                <HeaderMobile/>
                <GridSchoolMobile/>
                <Roadmap/>
                <Faq/>
                <SchoolTeam/>
                <Footer/>
            </Grid>
        );
    }

};

interface AlertState {
  open: boolean;
  message: string;
  severity: "success" | "info" | "warning" | "error" | undefined;
}

export default Home;
